import b2bFooter from "../../components/b2bfooter.vue";
export default{
	components:{
		b2bFooter			
	},
	data:function(){
		return {
			pageData:[],
			type:"all"
		}
	},
	onLoad:function(ops){
		uni.setNavigationBarTitle({
			title:"我的订单"
		})
		if(ops.type!=undefined){
			this.type=ops.type;
		}
		this.getPage();
	},
	methods:{
		goGuest:function(id,shopid){
			uni.navigateTo({
				url:"../b2b_guest/user?shopid="+shopid+"&id="+id
			})
		},
		goShop:function(shopid){
			uni.navigateTo({
				url:"../b2b_shop/index?shopid="+shopid
			})
		},
		getPage:function(){
			var that=this;
			that.app.get({
				url:that.app.apiHost+"/module.php?m=b2b_order&a=my&ajax=1",
				data:{
					type:that.type,
					authcode:that.app.getAuthCode()
				},
				dataType:"json",
				success:function(res){
				 
					that.pageData=res.data;
				}
			})
		},
		goOrder:function(orderid){
			uni.navigateTo({
				url:"../b2b_order/show?orderid="+orderid
			})
		},
		setType:function(t){
			this.type=t;
			this.getPage();
		},
		pay:function(orderid){
			uni.navigateTo({
				url:"../b2b_order/pay?orderid="+orderid
			})
		}
	}
}
